.search {
  &-form {
    position: relative;

    &__input {
      padding: 24px 24px 24px 70px;
      width: 100%;
      border-radius: 12px;
      border: 1px solid #efefef;

      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;
      color: #70737c;
    }

    &__btn {
      position: absolute;
      left: 34px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
