.filter {
  padding: 28px 0 62px;

  &-wrapper {
    display: flex;
  }

  .aside {
    &-content {
      &__item {
        padding-bottom: 18px;
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid #eeeeee;
      }

      &__title {
        position: relative;
        margin-bottom: 18px;
        font-weight: 600;
        font-size: 18px;
        line-height: 18px;
        letter-spacing: -0.04em;
        color: #1d1c2d;

        i {
          position: absolute;
          top: 15%;
          right: 2px;
          border: solid #bab9bf;
          border-width: 0 2px 2px 0;
          display: inline-block;
          padding: 4px;
          transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
          cursor: pointer;
        }
      }

      &__btn {
        display: flex;
        align-items: center;
        padding: 10px;
        width: 255px;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: -0.04em;

        color: #4d4c59;
        border-radius: 12px;
        transition: all 0.5s ease;
        box-shadow: 0px 4px 39px rgba(203, 203, 203, 0.25);

        &:hover {
          box-shadow: 0px 4px 39px rgba(203, 203, 203, 0.65);
        }

        &--cat {
          border: 1px solid transparent;
          &:hover {
            border: 1px solid #ffbb37;
          }
        }

        &--dog {
          border: 1px solid transparent;
          &:hover {
            border: 1px solid #46bf8c;
          }
        }

        .icon-cat {
          margin-right: 15px;
          font-size: 22px;
          color: #ffbb37;
        }

        .icon-dog {
          margin-right: 15px;
          font-size: 22px;
          color: #46bf8c;
        }
      }
    }

    &-range {
      display: flex;
      margin-bottom: 20px;

      &__slide {
        position: relative;
        display: inline-flex;
        flex-direction: column;
      }

      &__input {
        padding: 13px 12px 13px 18px;
        border: 1px solid #efefef;
        border-radius: 7px;
      }

      &__dollar {
        position: absolute;
        top: 33px;
        left: 9px;
      }
    }
  }
}
.aside-range__slide + .aside-range__slide {
  margin-left: 40px;
}

.aside-content__item + .aside-content__item {
  margin-top: 9px;
}

.aside-content__btn + .aside-content__btn {
  margin-top: 8px;
}

.noUi {
  &-target {
    background-color: #efefef;
    border-radius: 59px;
  }

  &-connects {
    border-radius: 59px;
  }

  &-connect {
    background-color: #ffbb37;
  }

  &-horizontal {
    height: 10px;

    .noUi-handle {
      width: 19px;
      height: 19px;
      background-color: #ffbb37;
      border: 1px solid #ffffff;
      border-radius: 100%;
      box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.25);

      &::before,
      &:after {
        content: none;
      }
    }
  }
}
