@import "base/variables";
@import "base/btns";
@import "base/animation-shape";
@import "base/fonts-awesome";
@import "libs/range-slider";
@import "libs/nouislider.min";

* {
  box-sizing: border-box;
}
body,
ul,
li,
a,
h1,
h2,
h3,
h4,
h5,
h6,
button,
p {
  font-family: $poppins;
  margin: 0;
  padding: 0;
}
.container {
  padding-right: 20px;
  padding-left: 20px;
  margin: 0 auto;
  max-width: 1140px;
  width: 100%;
}

a {
  display: inline-block;
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  outline: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

input {
  outline: none;
}

img,
span,
svg {
  display: inline-block;
}

@import "blocks/header";
@import "blocks/main";
@import "blocks/product";
@import "blocks/shelter";
@import "blocks/news";
@import "blocks/faq";
@import "blocks/reviews";
@import "blocks/ident";
@import "blocks/footer";
@import "blocks/our-product";
@import "blocks/search";
@import "blocks/filter";
@import "base/media";
