.ident {
  overflow: hidden;
  &-wrapper {
    position: relative;
    padding: 41px 0 59px;
    display: flex;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 364px;
      height: 548px;
      background-color: rgba(235, 255, 153, 0.44);
      filter: blur(198px);
      z-index: -1;
    }
  }

  &-content {
    &__img {
      width: 540px;

      img {
        max-width: 100%;
      }
    }

    &__title {
      margin-bottom: 16px;
      font-weight: 600;
      font-size: 32px;
      line-height: 45px;
      letter-spacing: -0.04em;
      color: #1f2533;
    }

    &__text {
      margin-bottom: 52px;
      font-family: $inter;
      font-size: 16px;
      line-height: 28px;
      color: #1d1c2d;
    }

    &__btns {
      display: flex;
      justify-content: center;
    }
  }

  &-items {
    font-size: 0;
  }

  &-item {
    & + & {
      margin-left: 71px;
    }

    display: inline-block;
    font-family: $inter;

    &__title {
      margin-bottom: 8px;
      font-weight: bold;
      font-size: 32px;
      line-height: 28px;
      letter-spacing: -0.06em;
      color: #1d1c2d;
    }

    &__subtitle {
      font-size: 16px;
      line-height: 28px;
      color: #1d1c2d;
    }
  }
}
