.reviews {
  &-wrapper {
    position: relative;
    padding: 87px 0 42px;

    &::before {
      position: absolute;
      content: "";
      width: 344px;
      top: 216px;
      left: 320px;
      height: 344px;
      background-color: #ffdfc8;
      filter: blur(198px);
      z-index: -1;
    }

    &::after {
      position: absolute;
      content: "";
      width: 344px;
      height: 344px;
      top: 84px;
      right: 0px;
      background-color: rgba(173, 255, 91, 0.46);
      filter: blur(198px);
      z-index: -1;
    }
  }

  &-info {
    width: 350px;

    &__title {
      font-weight: 600;
      font-size: 32px;
      line-height: 45px;
      letter-spacing: -0.04em;
      color: #1f2533;
    }

    &__text {
      font-size: 18px;
      line-height: 32px;
      letter-spacing: -0.04em;
      color: #4d4c59;
    }
  }

  &-content {
    background-color: #fff;
    padding: 28px 11px 19px 24px;
    width: 350px;
    box-shadow: 0px 4px 34px rgba(101, 101, 101, 0.17);
    border-radius: 12px;

    &__text {
      margin-bottom: 26px;
    }

    &__author {
      display: flex;
      align-items: center;

      a {
        display: flex;
        flex-direction: column;
        margin-left: 11px;
        flex-grow: 1;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: -0.04em;
        color: #1f2533;

        span {
          font-family: $inter;
          font-size: 14px;
          line-height: 19px;
          color: #70737c;
        }
      }

      .star {
        position: relative;
        padding-left: 29px;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: -0.06em;
        color: #4d4c59;

        &::before {
          position: absolute;
          content: url("../../img/main-page/bigStar.png");
          top: 2px;
          left: 0;
        }
      }
    }
  }

  &-mobile {
    display: none;
  }
}

.grid {
  &-item {
    margin-bottom: 30px;
    margin-right: 10px;

    &:nth-child(2) {
      margin-top: 50px;
    }

    &:nth-child(3n) {
      margin-right: 0px;
    }
  }
}
