// ==== Header ==== //

@media (max-width: 1200px) {
  .header {
    &-wrapper {
      margin-left: 0px; //
    }

    &-inputs {
      margin-left: auto;
    }
  }

  .main {
    &-wrapper {
      &::before {
        // left: 150px;
      }
      &::after {
        right: 0;
      }
    }
  }
}

@media (max-width: 1139px) {
  .product {
    &-items {
      justify-content: center;
    }

    &-item {
      &:nth-child(2n) {
        margin-left: 0px;
        margin-right: 0;
      }
      &:nth-child(3n) {
        margin-right: 25px;
      }
    }
  }
}

@media (max-width: 1060px) {
  .header {
    &-socials {
      margin-right: 22px;
    }
  }
}

@media (max-width: 992px) {
  .nav {
    &-item {
      & + & {
        margin-left: 25px;
      }

      &__link {
        font-size: 14px;
      }
    }
  }

  .main {
    &-wrapper {
      background-size: 500px 500px;
    }

    &__title {
      max-width: 440px;
      font-size: 36px;
    }

    &__text {
      max-width: 346px;
    }
  }
}

@media (max-width: 930px) {
  .header {
    &-socials {
      margin-left: auto;
      margin-right: 0px;
    }
    &-icons {
      margin-left: auto;
    }
    &-inputs {
      display: none;
    }
  }

  .shelter {
    &-wrapper {
      flex-direction: column;
      &::before {
        content: none;
      }
    }

    &-content {
      & + & {
        margin-left: 0px;
        margin-top: 28px;
      }

      &__btn {
        margin-top: 24px;
      }

      &:first-child {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }
}

@media (max-width: 905px) {
  .ident {
    &-wrapper {
      flex-direction: column;

      &::before {
        content: none;
      }
    }
    &-content {
      &__img {
        display: flex;
        justify-content: center;
        width: 100%;
      }

      &:first-child {
        order: 1;
      }
    }
  }
}

@media (max-width: 866px) {
  .main {
    &-wrapper {
    }
    &__text {
      max-width: 300px;
    }
    &-cards {
      &__item {
        & + & {
          margin-left: 10px;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .container {
    &-slide {
      padding-right: 0;
    }
  }
  .main {
    &-wrapper {
      background: none;

      &:before {
        left: 0;
      }
    }

    &__title {
      max-width: 100%;
    }
    &__text {
      max-width: 100%;
    }

    &-image {
      display: block;
    }

    &-cards__item {
      & + & {
        margin-left: 0;
      }
    }
  }

  .product {
    &-box {
      &__title {
        font-size: 25px;
      }

      &__btn {
        display: none;
      }
    }

    &-items {
      justify-content: initial;
      flex-wrap: nowrap;
    }

    &-item {
      margin-right: 0;
      &:nth-child(2n) {
        margin-left: 0px;
        margin-right: 0;
      }
      &:nth-child(3n) {
        margin-right: 0px;
      }
    }

    &-dog--hidden-show {
      display: none;
    }
  }

  .faq {
    &-wrapper {
      background: none;

      &::after {
        top: 50%;
      }
    }

    &__image {
      display: flex;
    }
  }

  .reviews {
    .container {
      padding-right: 0px;
    }
    &-wrapper {
      .grid {
        display: none;
      }
      &::before,
      &::after {
        content: none;
      }
    }

    &-mobile {
      display: block;
      .reviews-info {
        margin-bottom: 26px;
        width: 100%;
        padding-right: 20px;
        text-align: center;
      }
    }
  }

  .footer {
    &-content {
      &:nth-child(2) {
        margin-left: 20px;
      }
    }
  }
}

@media (max-width: 686px) {
  .news {
    &-item {
      position: initial;
      width: 311px;
      height: auto;

      &__content {
        position: initial;
        width: 311px;
        min-height: 196px;

        padding: 18px;
        width: 311px;
        background: #ffffff;
        box-shadow: 0px 4px 15px rgba(174, 174, 174, 0.25);
        backdrop-filter: blur(20px);
        border-radius: 12px;
      }

      &__date {
        color: #bab9bf;
      }

      &__title {
        font-size: 18px;
        line-height: 28px;
        letter-spacing: -0.04em;
        color: #1d1c2d;
      }

      &__author {
        color: #bab9bf;
      }

      &__link {
        padding: 0px 0px;
        background-color: transparent;
        border-radius: 0px;
        color: #ffbb37;
      }
    }

    &-slider {
      padding-bottom: 0px;
    }

    .swiper-pagination {
      display: none;
    }
  }
}

@media (max-width: 644px) {
  .nav {
    &-item {
      & + & {
        margin-left: 15px;
      }
    }
  }
}

@media (max-width: 644px) {
  .header {
    &-socials {
      display: none;
    }
  }
}

@media (max-width: 581px) {
  .shelter {
    &-content {
      &__img {
        height: 216px;
        width: 345px;
      }
    }

    &-item {
      & + & {
        margin-left: 20px;
      }
      &__title {
        font-size: 24px;
        line-height: 28px;
      }

      &__subtitle {
        font-size: 14px;
        line-height: 19px;
      }
    }
  }
}

@media (max-width: 539px) {
  .faq {
    &-accordeon {
      &__control {
        width: 86%;
      }

      &__arrow {
        display: none;
      }
    }

    &-mobile {
      display: block;
    }
  }

  .footer {
    &-wrapper {
      flex-direction: column;
    }

    &-content {
      max-width: 100%;
      &:nth-child(2) {
        margin-left: 0px;
      }
      &:nth-child(3) {
        margin-left: 0px;
      }

      & + & {
        margin-top: 39px;
      }
    }

    &-block {
      flex-direction: column-reverse;
      align-items: center;
      &__privacy {
        margin-bottom: 10px;
      }
    }
  }
}

@media (max-width: 510px) {
  .header {
    &-socials {
      display: none;
    }
  }

  .nav {
    &-item {
      &__link {
        font-size: 14px;
      }
    }
  }
}

@media (max-width: 510px) {
  .header {
    &-menu {
      display: flex;
    }
  }
  .nav {
    display: none;
  }
}

@media (max-width: 392px) {
  .main {
    &-wrapper {
      &:after {
        width: 250px;
        height: 250px;
      }
      &::before {
        width: 250px;
        height: 250px;
      }
    }

    &__btns {
      display: inline-flex;
      flex-direction: column;
    }
  }

  .btn {
    & + & {
      margin-left: 0;
      margin-top: 15px;
    }
  }

  .product {
    &-item {
      width: 311px;
    }
  }

  .ident {
    &-content {
      &__btns {
        flex-direction: column;
      }
    }
  }

  .btn {
    align-self: flex-start;
  }
}
