.shelter {
  overflow: hidden;

  &-wrapper {
    position: relative;
    padding: 41px 0;
    display: flex;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      width: 364px;
      height: 548px;
      background-color: rgba(235, 255, 153, 0.44);
      filter: blur(198px);
      z-index: -1;
    }
  }

  &-content {
    & + & {
      margin-left: 30px;
    }

    &__img {
      margin-bottom: 32px;
      width: 540px;
      height: 339px;

      img {
        max-width: 100%;
      }
    }

    &__title {
      margin-bottom: 16px;
      font-weight: 600;
      font-size: 32px;
      line-height: 45px;
      letter-spacing: -0.04em;
      color: #1f2533;
    }

    &__text {
      & + & {
        margin-top: 32px;
      }
      font-family: $inter;
      font-size: 16px;
      line-height: 28px;
      color: #1d1c2d;
    }

    &__btn {
      margin-top: 52px;
      padding: 16px 26px;
      background-color: #46bf8c;
      box-shadow: 0px 4px 20px rgba(70, 191, 140, 0.46);
      border-radius: 12px;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;
      color: #ffffff;
      transition: all 0.3s linear;

      &:hover {
        background-color: #17bf79;
        box-shadow: 0px 4px 22px rgba(23, 191, 121, 0.61),
          0px 4px 20px rgba(70, 191, 140, 0.46);
      }
    }
  }

  &-items {
    font-size: 0;
  }

  &-item {
    & + & {
      margin-left: 71px;
    }

    display: inline-block;
    font-family: $inter;

    &__title {
      margin-bottom: 8px;
      font-weight: bold;
      font-size: 32px;
      line-height: 28px;
      letter-spacing: -0.06em;
      color: #1d1c2d;
    }

    &__subtitle {
      font-size: 16px;
      line-height: 28px;
      color: #1d1c2d;
    }
  }
}
