.main {
  overflow: hidden;

  &-wrapper {
    background: url("../../img/main-page/main-back.png") no-repeat top right;
    padding-top: 63px;
    padding-bottom: 50px;

    position: relative;
    &::before {
      position: absolute;
      content: "";
      width: 344px;
      top: 216px;
      left: 320px;
      height: 344px;
      background-color: #ffdfc8;
      filter: blur(198px);
      z-index: -1;
    }

    &::after {
      position: absolute;
      content: "";
      width: 344px;
      height: 344px;
      top: 54px;
      right: -51px;
      background-color: rgba(173, 255, 91, 0.46);
      filter: blur(198px);
      z-index: -1;
    }
  }
  &__suptitle {
    margin-bottom: 6px;
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.04em;
    color: #4d4c59;
  }

  &__title {
    max-width: 603px;
    margin-bottom: 6px;
    font-weight: bold;
    font-size: 52px;
    line-height: 64px;
    letter-spacing: -0.04em;
    color: #1d1c2d;
  }

  &__text {
    max-width: 456px;
    margin-bottom: 11px;
    font-family: $inter;
    font-size: 16px;
    line-height: 28px;
    color: #1d1c2d;
  }

  &__btns {
    font-size: 0;
    margin-bottom: 55px;
  }

  &-cards {
    display: flex;

    &__item {
      position: relative;
      & + & {
        margin-left: 30px;
      }

      padding: 16px 15px 17px 56px;
      max-width: 255px;
      min-height: 140px;
      background-color: rgba(255, 255, 255, 0.8);
      border: 1px solid #fff5e2;
      box-shadow: 0px 4px 15px rgba(174, 174, 174, 0.25);
      backdrop-filter: blur(10px);
      border-radius: 12px;

      svg {
        position: absolute;
        top: 21px;
        left: 19px;
        font-size: 20px;

        path {
          fill: #46bf8c;
        }
      }
    }

    &__title {
      margin-bottom: 4px;
      font-weight: 600;
      font-size: 18px;
      line-height: 28px;
      letter-spacing: -0.04em;
      color: #1d1c2d;
    }

    &__text {
      font-family: $inter;
      font-size: 14px;
      line-height: 19px;
      color: #4d4c59;
    }
  }
  &-image {
    display: none;
    &__img {
      max-width: 100%;
    }
  }
}
