.btn {
  & + & {
    margin-left: 12px;
  }
  padding: 16px 26px;
  border-radius: 12px;
  display: inline-flex;
  align-items: center;
  transition: all 0.5s ease;

  .icon-cat,
  .icon-dog {
    font-size: 22px;
    color: #fff;
  }

  span {
    margin-left: 16px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.04em;
    color: #ffffff;
  }

  &-cat {
    background-color: #ffbb37;
    box-shadow: 0px 4px 20px rgba(255, 187, 55, 0.64);

    &:hover {
      background-color: #ffa337;
      box-shadow: 0px 4px 20px rgba(211, 133, 16, 0.64);
    }
  }

  &-dog {
    background: #46bf8c;
    box-shadow: 0px 4px 20px rgba(70, 191, 140, 0.46);

    &:hover {
      background-color: #17bf79;
      box-shadow: 0px 4px 22px rgba(23, 191, 121, 0.61);
    }
  }
}
