@font-face {
  font-family: "icons";
  src: url("../../fonts/icons.eot?45xkmf");
  src: url("../../fonts/icons.eot?45xkmf#iefix") format("embedded-opentype"),
    url("../../fonts/icons.woff?45xkmf") format("woff"),
    url("../../fonts/icons.svg?45xkmf#icons") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"]::before,
[class*=" icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icons" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

