.header {
  box-shadow: 0px 4px 25px rgba(217, 217, 217, 0.25);
  &-wrapper {
    margin-left: 80px;
    display: flex;
    align-items: center;
    padding: 13px 0;
  }

  &-socials {
    display: flex;
    align-items: center;
    margin-left: 37px;
    margin-right: 75px;
    height: 20px;

    &__link {
      & + & {
        margin-left: 10px;
      }
      font-size: 20px;
      opacity: 0.7;
      transition: all 0.5s linear;

      &:hover {
        opacity: 1;
      }
    }

    i {
      color: #70737c;
    }
  }

  &-icons {
    display: flex;
    align-items: center;
    &__link {
      position: relative;
      width: 45px;
      height: 45px;
      color: #70737c;
      font-size: 20px;
      border: 1px solid #eeeeee;
      box-sizing: border-box;
      border-radius: 100%;

      & + & {
        margin-left: 10px;
      }
    }

    &__pic {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      &::before {
        color: #bab9bf;
      }
    }
  }

  &-inputs {
    margin-left: 17px;
    position: relative;
    &__btn {
      position: absolute;
      left: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
    &__search {
      width: 285px;
      padding: 12px 20px 12px 60px;
      border: 1px solid #efefef;
      box-sizing: border-box;
      border-radius: 71px;
      font-size: 14px;
      line-height: 19px;
      color: #70737c;
    }
  }
  &-menu {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: 32px;

    &__item {
      & + & {
        margin-top: 4px;
      }
      background-color: rgba(112, 115, 124, 1);
      height: 2px;
      width: 15px;

      &:first-child {
        width: 8px;
      }
      &:last-child {
        width: 12px;
      }
    }
  }

  &-menu {
    display: none;
  }
}

.nav {
  &-items {
    display: flex;
  }

  &-item {
    & + & {
      margin-left: 38px;
    }

    &__link {
      font-family: $inter;
      font-weight: 500;
      font-size: 16px;
      line-height: 28px;
      color: #4d4c59;
      border-bottom: 2px solid transparent;
      transition: all 0.3s linear;

      &:hover {
        border-bottom: 2px solid #4d4c59;
      }

      h1 {
        font-family: inherit;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        color: inherit;
      }
    }
  }
}

.mobile {
  padding: 35px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: #fff;
  box-shadow: 2px black;
  transform: translateX(-120%);
  width: 270px;
  border-bottom-right-radius: 15px;
  box-shadow: 0px 0px 20px 4px black;
  transition: all 0.5s ease;

  &-items {
    text-align: center;
  }

  &-item {
    & + & {
      margin-top: 10px;
    }

    &__link {
      font-family: $inter;
      font-weight: 500;
      font-size: 16px;
      color: #4d4c59;

      h1 {
        font-family: inherit;
        font-weight: inherit;
        line-height: inherit;
        color: inherit;
      }
    }
  }

  &-socials {
    margin-top: 15px;
    text-align: center;

    &__link {
      display: block;

      & + & {
        margin-top: 15px;
      }
    }
  }

  &-open {
    transform: translateX(0%);
  }
}
