.faq {
  &-wrapper {
    position: relative;
    padding-top: 64px;
    padding-bottom: 47px;
    background: url("../../img/main-page/faq.png");
    background-repeat: no-repeat;
    background-position: 100% 10px;

    &::after {
      position: absolute;
      content: "";
      top: 0;
      right: 0;
      width: 248px;
      height: 344px;
      background-color: rgba(255, 202, 123, 0.46);
      filter: blur(198px);
      z-index: -1;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 45px;
    letter-spacing: -0.04em;
    color: #1d1c2d;
  }

  &-accordeon {
    & + & {
      margin-top: 16px;
    }

    max-width: 730px;
    position: relative;
    background: #ffffff;
    border: 1px solid #eeeeee;
    box-sizing: border-box;
    border-radius: 12px;
    transition: all 0.5s ease;

    &__arrow {
      top: 18px;
      right: 20px;
      position: absolute;
      width: 32px;
      height: 32px;
      background-color: #dff1ed;
      border-radius: 7px;
      cursor: pointer;
      transition: background-color 0.3s ease-out;

      &::before {
        position: absolute;
        content: "";
        top: 10px;
        left: 11px;
        padding: 3px;
        border: solid #46bf8c;
        border-width: 0 3px 3px 0;
        display: inline-block;
        transform: rotate(45deg);
        transition: transform 0.3s ease-out;
      }
    }

    &-items {
      margin-top: 32px;
    }

    &__control {
      padding: 20px 0px 20px 24px;
      width: 90%;

      &--pressed {
        .faq-accordeon__arrow {
          background-color: #46bf8c;
          transition: background-color 0.3s ease-out;

          &::before {
            padding: 3px;
            top: 13px;
            border: solid #fff;
            border-width: 0 3px 3px 0;
            transform: rotate(225deg);
            transition: transform 0.3s ease-out;
          }
        }
      }
    }

    &__title {
      font-weight: 500;
      font-size: 18px;
      line-height: 32px;
      letter-spacing: -0.04em;
      color: #1d1c2d;
    }

    &__content {
      box-sizing: content-box;
      max-height: 0;
      opacity: 0;
      padding: 0px 100px 0px 24px;
      will-change: max-height;
      transition: opacity 0.3s linear, max-height 0.3s linear;

      &--open {
        max-height: 0;
        opacity: 1;
        padding: 0px 100px 20px 24px;
        will-change: max-height;
        transition: opacity 0.6s linear, max-height 0.3s linear;
      }
    }

    &__text {
      max-height: inherit;
      font-family: $inter;
      font-size: 16px;
      line-height: 28px;
      color: #4d4c59;
    }
  }

  &-mobile {
    display: none;
    position: relative;
    height: 32px;
    background-color: #dff1ed;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    transition: background-color 0.3s ease-out;

    &__arrow {
      position: absolute;
      content: "";
      top: 14px;
      left: 50%;
      padding: 3px;
      border: solid #46bf8c;
      border-width: 0 3px 3px 0;
      display: inline-block;
      transform: rotate(45deg) translateX(-50%);
      transition: transform 0.3s ease-out;
    }

    &--pressed {
      background-color: #46bf8c;

      .faq-mobile__arrow {
        border: solid #fff;
        border-width: 0 3px 3px 0;
        top: 13px;
        transform: rotate(225deg);
      }
    }
  }

  &__image {
    display: none;
    justify-content: center;
    img {
      max-width: 100%;
    }
  }
}
