.news {
  overflow-x: hidden;
  &-wrapper {
    padding: 70px 0 45px;
  }

  &__title {
    margin-bottom: 32px;
    font-weight: 600;
    font-size: 32px;
    line-height: 45px;
    letter-spacing: -0.04em;
    color: #1d1c2d;
  }

  &-slider {
    position: relative;
    padding-bottom: 57px;
  }

  &-items {
    display: flex;
  }

  &-item {
    position: relative;
    width: 539px;
    height: 467px;

    &__img {
      width: inherit;
      height: inherit;

      img {
        max-width: 100%;
      }
    }

    &__content {
      padding: 21px 31px;
      position: absolute;
      left: 14px;
      bottom: 15px;
      right: 15px;
      width: 503px;
      background-color: rgba(44, 44, 44, 0.3);
      box-shadow: 0px 4px 15px rgba(174, 174, 174, 0.25);
      border-radius: 12px;
      backdrop-filter: blur(9px);
    }

    &__date {
      margin-bottom: 7px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;
      color: #ffffff;
    }
    &__title {
      margin-bottom: 14px;
      font-weight: 500;
      font-size: 24px;
      line-height: 32px;
      letter-spacing: -0.04em;
      color: #ffffff;
    }

    &__box {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__author {
      position: relative;
      padding-left: 42px;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;
      color: #ffffff;

      &::before {
        position: absolute;
        content: url("../../img/main-page/author.png");
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        width: 32px;
        height: 32px;
      }
    }
    &__link {
      padding: 18px 34px;
      background-color: #dff1ed;
      border-radius: 74px;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      text-align: center;
      letter-spacing: -0.04em;
      color: #46bf8c;
    }
  }
}

// Pagination
.news-slider {
  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    background-color: #eeeeee;
    opacity: 1;
  }

  .swiper-pagination-bullet-active {
    background-color: #ffbb37;
  }

  .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
  .swiper-pagination-horizontal.swiper-pagination-bullets
    .swiper-pagination-bullet {
    margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 8px);
  }
}
