.footer {
  &-wrapper {
    padding-top: 38px;
    padding-bottom: 22px;
    display: flex;
  }

  &-top {
    border-top: 1px solid #eeeeee;
  }
  &-content {
    & + & {
      margin-left: 30px;
    }
    &:nth-child(2) {
      margin-left: 94px;
    }

    max-width: 255px;

    &__text {
      margin-top: 71px;
      margin-bottom: 24px;
      font-family: $inter;
      font-size: 14px;
      line-height: 24px;
      font-feature-settings: "salt" on, "liga" off;
      color: #4d4c59;
    }

    &__title {
      margin-bottom: 24px;
      font-weight: 500;
      font-size: 20px;
      line-height: 30px;
      font-feature-settings: "salt" on, "liga" off;
      color: #1d1c2d;
    }
  }

  &-socials {
    display: flex;

    &__item {
      position: relative;
      & + & {
        margin-left: 27px;
      }
      &::after {
        position: absolute;
        content: "";
        width: 24px;
        height: 24px;
        left: -5px;
        top: -1px;
        background-color: #ffbb37;
        border-radius: 100%;
        z-index: -1;
        opacity: 0.2;
      }
    }

    &__link {
      position: relative;
      color: #ffbb37;

      i {
        &::before {
          color: #ffbb37;
        }
      }
    }
  }
  &-nav {
    &__item {
      & + & {
        margin-top: 12px;
      }
    }

    &__link {
      font-family: $inter;
      font-size: 14px;
      line-height: 24px;
      font-feature-settings: "salt" on, "liga" off;
      color: #4d4c59;
    }
  }

  &-contacts {
    &__item {
      & + & {
        margin-top: 24px;
      }

      &:nth-child(2) {
        .footer-contacts__link {
          img {
            left: 5px;
          }
        }
      }

      &:nth-child(3) {
        .footer-contacts__link {
          img {
            left: 3px;
          }
        }
      }
    }

    &__link {
      padding-left: 28px;
      position: relative;
      font-size: 14px;
      line-height: 24px;
      font-feature-settings: "salt" on, "liga" off;
      color: #4d4c59;

      i {
        position: absolute;
        left: 0;
        &::before {
          font-size: 20px;
          color: #46bf8c;
        }
      }

      .icon-mobile {
        left: 3px;
      }
      .icon-location {
        left: 2px;
      }
    }
  }

  &-down {
    background-color: #ffbb37;
  }

  &-block {
    display: flex;
    justify-content: space-between;
    padding-top: 25px;
    padding-bottom: 25px;
  }

  &-block {
    &__copyright {
      font-family: $inter;
      font-size: 14px;
      line-height: 24px;
      font-feature-settings: "salt" on, "liga" off;
      color: #ffffff;

      span {
        text-transform: uppercase;
      }
    }

    &__privacy {
      font-size: 0;
      a,
      span {
        font-family: $inter;
        font-size: 14px;
        line-height: 24px;
        font-feature-settings: "salt" on, "liga" off;
        color: #ffffff;
      }

      a {
        margin-left: 8px;
        margin-right: 8px;

        &:first-child {
          margin-left: 0px;
        }

        &:last-child {
          margin-right: 0px;
        }
      }
    }
  }
}
