.product {
  &-wrapper {
    padding-top: 36px;
    padding-bottom: 40px;
  }

  &-dog {
    margin-top: 40px;
  }

  &-box {
    display: flex;
    align-items: center;

    &__icon {
      margin-right: 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 48px;
      height: 48px;
      border-radius: 13px;

      &--cat {
        background-color: #fff5e2;
      }

      &--dog {
        background-color: #dff1ed;
      }

      .icon-cat {
        font-size: 22px;
        &::before {
          color: #ffbb37;
        }
      }

      .icon-dog {
        font-size: 22px;
        &::before {
          color: #46bf8c;
        }
      }
    }
    &__title {
      font-weight: 600;
      font-size: 32px;
      line-height: 45px;
      letter-spacing: -0.04em;
      color: #1d1c2d;
    }

    &__btn {
      margin-left: auto;
      padding: 18px 34px;
      background-color: #dff1ed;
      border-radius: 74px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      text-align: center;
      letter-spacing: -0.04em;
      color: #46bf8c;
      transition: all 0.5s ease;

      &:hover {
        background-color: #46bf8c;
        color: #fff;
      }

      span:first-child {
        margin-right: 5px;
      }
    }
  }

  &-content {
    margin-top: 40px;
  }

  &-cat--hidden {
    margin-top: 0px;
    display: none;
    flex-wrap: wrap;

    &-show {
      display: flex;
    }
  }

  &-dog--hidden {
    margin-top: 0px;
    display: none;

    &-show {
      display: flex;
    }

    &-hide {
      display: none;
    }
  }

  &-items {
    display: flex;
    flex-wrap: wrap;
  }

  &-item {
    position: relative;
    margin-bottom: 15px;
    margin-right: 25px;
    &:nth-child(3n) {
      margin-right: 0;
    }
    padding: 24px 21px 24px;
    width: 350px;
    height: 560px;
    box-shadow: 0px 4px 39px rgba(203, 203, 203, 0.25);
    border-radius: 8px;

    &:hover {
      box-shadow: 0px 4px 34px rgba(101, 101, 101, 0.17);
    }

    &--pressed {
      border: 1px solid #eeeeee;
      box-shadow: none;
    }

    &__link {
      position: relative;
    }

    &__top {
      position: absolute;
      left: 7px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;
      z-index: 9;

      &--hot {
        color: #ffbb37;
        &::after {
          position: absolute;
          top: -5px;
          left: -13px;
          padding: 15px 27px;
          content: "";
          background-color: #fff5e2;
          border-radius: 49px;
          z-index: -5;
        }
      }

      &--discount {
        color: #cc3a3a;
        &::after {
          padding: 15px 27px;
          position: absolute;
          content: "";
          top: -5px;
          left: -15px;
          background-color: #ffe9e9;
          border-radius: 49px;
          z-index: -5;
        }
      }
    }

    &__img {
      margin-bottom: 8px;
      text-align: center;
      img {
        width: 307px;
        height: 307px;
      }
    }

    &__stars {
      margin-bottom: 8px;
    }

    &__title {
      margin-bottom: 14px;
      font-weight: 600;
      font-size: 18px;
      line-height: 28px;
      letter-spacing: -0.04em;
      color: #4d4c59;
    }

    &__price {
      margin-bottom: 25px;
      font-family: $inter;
      font-weight: bold;
      font-size: 32px;
      line-height: 28px;
      letter-spacing: -0.06em;
      color: #1d1c2d;
    }

    &__discount {
      position: relative;
      margin-left: 16px;
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      letter-spacing: -0.06em;
      color: #bab9bf;

      &::before {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        content: "";
        width: 52px;
        height: 2px;
        background-color: #bab9bf;
      }
    }

    &__btn {
      display: flex;
      align-items: center;
      background-color: #fff;
      padding: 14px 16px;
      border: 1px solid #eeeeee;
      box-sizing: border-box;
      border-radius: 12px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;

      transition: all 0.3s linear;

      .icon-basket {
        margin-right: 21px;
        font-size: 21px;
        &::before {
          color: #46bf8c;
        }
      }

      .icon-added {
        font-size: 24px;
        margin-right: 15px;
      }

      span {
        color: #46bf8c;
      }

      &:hover {
        background-color: #dff1ed;
      }

      &--pressed {
        border: none;
        background-color: #46bf8c;

        span {
          color: #fff;
        }

        &:hover {
          background-color: #46bf8c;
        }
      }
    }
    &__favourite {
      position: absolute;
      bottom: 36px;
      right: 50px;
      z-index: 99;

      &::after {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        content: url("../../img/main-page/heartpng.png");
        cursor: pointer;
      }
    }

    &__number {
      display: inline-block;
    }
  }
}

.product-item__favourite.product-item__favourite--pressed.product-item__favourite::after {
  content: url("../../img/main-page/heartGreen.png");
}
