.our-product {
  padding: 36px 0 18px;
  &-container {
    display: flex;
    justify-content: space-between;
  }

  &-card {
    overflow: hidden;
    padding: 17px 0 0 32px;
    display: flex;
    justify-content: space-between;
    width: 540px;
    max-height: 208px;
    border-radius: 12px;

    &--dog {
      background-color: #fff5e2;
    }

    &--cat {
      background-color: #dff1ed;
    }

    &__suptitle {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;
      opacity: 0.5;

      &--dog {
        color: #ffbb37;
      }

      &--cat {
        color: #46bf8c;
      }
    }

    &__title {
      font-weight: 600;
      font-size: 28px;
      line-height: 35px;
      letter-spacing: -0.04em;

      &--dog {
        color: #ffbb37;
      }

      &--cat {
        color: #46bf8c;
      }
    }
  }
}
